import React, { Component } from 'react';

import { Portfolio } from './Portfolio/Portfolio';
import { about } from '../Constants/About';
import { skills } from '../Constants/Skills';
import { projects } from '../Constants/Projects';

import Sky from 'react-sky'; 
import Robot from '../Constants/robot.svg'; 
import Cube from '../Constants/3d-design.svg'; 
import Laptop from "../Constants/result.svg";
import Server from '../Constants/server.svg';
import Setup from '../Constants/setup.svg'; 
import './App.css';


class App extends Component {
	constructor () {
		super();

		this.state = {
			selectedSection: "",
			selectedHeading: "",
			selectedQuote: "",
			about: null,
			skills: null,
			projects: null,
		};
	}

	componentWillMount () {
		this.setState ({ 
			about,
			skills,
			projects,
			selectedHeading: about.heading,
			selectedQuote: about.quote,
			selectedSection: "about" 
		});
	}

	handleSectionClick =  (section) => {
		this.setState({
			selectedSection: section,
			selectedHeading: this.state[section].heading,
			selectedQuote: this.state[section].quote
		})
	}

	render () {
		return (
			<div className="App">
				<Sky
				images={{
						/* FORMAT AS FOLLOWS */
						0: Cube, 
						1: Robot,
						2: Laptop,
						3: Server,
						4: Setup
					}}
					how={100} /* Pass the number of images Sky will render chosing randomly */
					time={50} /* time of animation */
					size={'50px'} /* size of the rendered images */
					background={'palettedvioletred'} /* color of background */
				/>
				<Portfolio
					handleSectionClick={this.handleSectionClick}
					selectedSection={this.state.selectedSection}
					selectedHeading={this.state.selectedHeading}
					selectedQuote={this.state.selectedQuote}
					about={this.state.about}
					skills={this.state.skills}
					projects={this.state.projects}
				/>
			</div>
		);
	}
}

export default App;
